@import url(../../styles/variable.css);
@import url(../../styles/fonts.css);
.switcher {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  flex-wrap: wrap;
  gap: 5px;
  &__item {
    width: calc(50% - 10px);
    background: var(--yellow);
